import React, { memo, Fragment, useState } from 'react'
import isEmpty from 'lodash/isEmpty'
import { ComposableMap, Geographies, Geography } from 'react-simple-maps'

import Tooltip from '../Tooltip'

const geoUrl = 'https://raw.githubusercontent.com/zcreativelabs/react-simple-maps/master/topojson-maps/world-110m.json'

const MapRaw = ({ countries, from, to }) => {
  const [tooltipData, setTooltipData] = useState(null)
  if (!countries) {
    return null
  }
  return (
    <Fragment>
      <ComposableMap
        data-tip=""
        projectionConfig={{
          rotate: [-10, 0, 0],
          scale: 140,
        }}
        width={800}
        height={400}
        style={{ width: '100%', height: 'auto' }}
      >
        {countries.length > 0 && (
          <Geographies geography={geoUrl}>
            {({ geographies }) =>
              geographies.map((geo) => {
                const d = countries.find((s) => s.key === geo.properties.ISO_A2)

                return (
                  <Geography
                    key={geo.rsmKey}
                    geography={geo}
                    fill={d && !isEmpty(d.metrics) ? '#FFC530' : '#EBEDF5'}
                    onMouseEnter={() => {
                      setTooltipData(d)
                    }}
                    onMouseLeave={() => {
                      setTooltipData(null)
                    }}
                  />
                )
              })
            }
          </Geographies>
        )}
      </ComposableMap>
      <Tooltip data={tooltipData} from={from} to={to} />
    </Fragment>
  )
}

export const Map = memo(MapRaw)
export default Map
