import React, { memo, useMemo, useCallback } from 'react'
import chunk from 'lodash/chunk'

import { getDictionaryByCode } from 'src/services'
import { commonConstants } from 'src/constants'

import Hint from 'src/indyComponents/Hint'

import styles from './styles.module.scss'
const { METRICS } = commonConstants

const MetricListRaw = ({ totals }) => {
  const formatMoney = (num) => {
    if (num > 1000000) {
      var si = [
        { value: 1, symbol: '' },
        { value: 1e3, symbol: 'k' },
        { value: 1e6, symbol: 'M' },
        { value: 1e9, symbol: 'G' },
        { value: 1e12, symbol: 'T' },
        { value: 1e15, symbol: 'P' },
        { value: 1e18, symbol: 'E' },
      ]
      var rx = /\.0+$|(\.[0-9]*[1-9])0+$/
      var i
      for (i = si.length - 1; i > 0; i--) {
        if (num >= si[i].value) {
          break
        }
      }
      return (num / si[i].value).toFixed(2).replace(rx, '$1') + si[i].symbol
    } else {
      return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ' ')
    }
  }

  const renderItem = useCallback((section, sectionIndex) => {
    if (!section) {
      return null
    }
    return section.map((item, itemIndex) => {
      const { key, name, value, info } = item
      const isLeft = sectionIndex === 0 && itemIndex === 0
      const isRight = sectionIndex === 2 && itemIndex === 1
      return (
        <div className={styles.item} key={key}>
          {info && (
            <div className={styles.hint}>
              <Hint content={info} isRight={isRight} isLeft={isLeft} />
            </div>
          )}
          <div className={styles.name}>{name}</div>
          <div className={styles.value}>{formatMoney(value)}</div>
          {/* <div className={cn(styles.persent, styles[`persent--${state}`])}>
            <DownIcon className={styles.arrow} /> <span className={styles.persentValue}>{persent} %</span>
          </div> */}
        </div>
      )
    })
  }, [])

  const renderItems = useMemo(() => {
    const data = Object.keys(totals).map((key) => {
      return {
        key,
        name: getDictionaryByCode(METRICS, key),
        value: totals[key],
      }
    })

    const sections = chunk(data, 2)

    return sections.map((section, index) => {
      if (!section) {
        return null
      }

      return (
        <div className={styles.col} key={index}>
          <div className={styles.section}>{renderItem(section, index)}</div>
        </div>
      )
    })
  }, [renderItem, totals])

  if (!totals) {
    return null
  }

  return (
    <div className={styles.common}>
      <div className={styles.row}>{renderItems}</div>
    </div>
  )
}

export const MetricList = memo(MetricListRaw)
export default MetricList
