import React, { memo, useCallback } from 'react'
import { NavLink } from 'react-router-dom'
import cn from 'classnames'

import Empty from '../Empty'
import Status from '../Status'

import styles from './styles.module.scss'

const TBodyRaw = (props) => {
  const { data, search } = props
  const renderText = useCallback((cell) => {
    return <span>{cell.prefix ? cell.prefix + cell.value : cell.value}</span>
  }, [])

  const renderType = useCallback(
    (cell) => {
      if (cell.type === 'LINK') {
        return (
          <td className={cn(styles.bodyCell)} key={cell.key}>
            <NavLink to={cell.to}>{renderText(cell)}</NavLink>
          </td>
        )
      }

      if (cell.type === 'EMPTY') {
        return (
          <td className={cn(styles.bodyCell)} key={cell.key} colSpan={cell.colSpan}>
            <Empty {...cell} />
          </td>
        )
      }
      if (cell.type === 'STATUS') {
        return (
          <td className={cn(styles.bodyCell)} key={cell.key}>
            <Status status={cell.value} />
          </td>
        )
      }

      return (
        <td className={styles.bodyCell} key={cell.key}>
          {renderText(cell)}
        </td>
      )
    },
    [renderText],
  )

  return (
    <tbody className={styles.body}>
      {Object.keys(data).map((id) => {
        const { key, value } = search

        if (search.value) {
          const searchField = data[id].find((item) => item.key === key)

          if (searchField) {
            if (!searchField.value.toLowerCase().includes(value.toLowerCase())) {
              return null
            }
          }
        }

        return (
          <tr className={styles.bodyRow} key={id}>
            {data[id].map((cell) => {
              return renderType(cell)
            })}
          </tr>
        )
      })}
    </tbody>
  )
}

export const TBody = memo(TBodyRaw)
export default TBody
