import React, { memo } from 'react'
import cn from 'classnames'
import { ReactComponent as SearchIcon } from 'src/assets/images/icons/search.svg'

import styles from './styles.module.scss'

const THeadRaw = (props) => {
  const { columns, onSearch } = props

  return (
    <thead className={styles.header}>
      <tr className={styles.headerRow}>
        {columns.map((column) => {
          const { key, name, width, isSearch } = column
          const props = {
            key,
          }

          return (
            <td style={{ width }} className={cn(styles.headerCell, {})} {...props}>
              {isSearch ? (
                <div className={cn(styles.headerItem, {})}>
                  <div className={cn(styles.search)}>
                    <SearchIcon className={cn(styles.searchIcon)} />
                    <input
                      type="text"
                      className={styles.searchInput}
                      placeholder={name}
                      onChange={(e) => onSearch({ key, value: e.target.value })}
                    />
                  </div>
                </div>
              ) : (
                <div className={cn(styles.headerItem, {})}>{name}</div>
              )}
            </td>
          )
        })}
      </tr>
    </thead>
  )
}

export const THead = memo(THeadRaw)
export default THead
