import React, { memo, Fragment, useMemo, useCallback, useEffect, useState } from 'react'
import moment from 'moment'

import { modalConstants, commonConstants } from 'src/constants'
import { getDictionary } from 'src/services'

import FullLoader from 'src/components/FullLoader'
import Download from 'src/components/Download'
import Accordeon from 'src/indyComponents/Accordeon'
import LinearChart from 'src/indyComponents/LinearChart'
import MapChart from 'src/indyComponents/MapChart'
import Report from 'src/indyComponents/Report'
import Filter from 'src/indyComponents/Filter'
import BaseButton from 'src/components/BaseButton'
import Dropdown from 'src/forms/Dropdown'
import { ReactComponent as EditIcon } from 'src/assets/images/icons/edit.svg'

import MetricList from '../../components/MetricList'

import { useMappedActions, useMappedState } from './bindings'
import styles from './styles.module.scss'
const { BRANDS, COUNTRIES, METRICS } = commonConstants

const StatisticAffiliateRaw = () => {
  const { countries, times, totals, isLoading } = useMappedState()
  const actions = useMappedActions()

  const [filter, setFilter] = useState({
    from: moment().subtract(29, 'days').format('YYYY-MM-DD'),
    to: moment().format('YYYY-MM-DD'),
    country: [],
    brand: [],
  })

  const allMetrics = useMemo(() => {
    return getDictionary(METRICS)
  }, [])

  const [selectedMetric, setSelectedMetric] = useState(allMetrics.find((item) => item.id === 'CLK') || {})

  useEffect(() => {
    actions.loadStats(filter)
  }, [actions, filter])

  // const onClickDelete = ({ presetName }) => {
  //   console.log('onClickDelete Statistic', presetName)
  // }

  const onChangeMetric = useCallback(
    (value = []) => {
      console.log('onChangeMetric value', value)
      setSelectedMetric(value[0] || {})
    },
    [setSelectedMetric],
  )

  // const onChangePreset = ({ value }) => {
  //   console.log('onChangePreset Statistic', value)
  // }

  const onChangeDate = useCallback(
    (value) => {
      const { from, to } = value
      const fromNext = from.format('YYYY-MM-DD')
      const toNext = to.format('YYYY-MM-DD')
      setFilter({ ...filter, from: fromNext, to: toNext })
    },
    [filter, setFilter],
  )

  const onChangeGeo = useCallback(
    (value) => {
      const nextCountry = value.map((item) => item.id)
      setFilter({ ...filter, country: nextCountry })
    },
    [filter, setFilter],
  )

  const onChangeBrands = useCallback(
    (value) => {
      const nextBrands = value.map((item) => item.id)
      setFilter({ ...filter, brand: nextBrands })
    },
    [filter, setFilter],
  )

  const filterFields = useMemo(() => {
    return [
      {
        type: 'date_range',
        label: 'Data Range',
        onChange: onChangeDate,
      },
      {
        type: 'geo',
        label: 'Geo',
        placeholder: 'All geo',
        options: getDictionary(COUNTRIES),
        onChange: onChangeGeo,
      },
      {
        type: 'brands',
        label: 'Brands',
        options: getDictionary(BRANDS),
        hasSearch: true,
        placeholderSearch: 'Search brand...',
        placeholder: 'All brands',
        onChange: onChangeBrands,
      },
    ]
  }, [onChangeDate, onChangeGeo, onChangeBrands])

  // const onClickEdit = useCallback(
  //   ({ value }) => {
  //     actions.showModal(modalConstants.EDIT_PRESET_MODAL, { presetName: 123132, onClickDelete })

  //     console.log('onChangePreset Statistic', value)
  //   },
  //   [actions],
  // )

  const rightPartMetrics = useMemo(() => {
    return (
      <div className={styles.metrics}>
        <Dropdown
          onChange={(value) => onChangeMetric(value)}
          defaultValue={[selectedMetric]}
          placeholder="Selected metric"
          options={allMetrics}
          isSimple
        />
      </div>
    )
  }, [selectedMetric, allMetrics, onChangeMetric])

  // const rightPartReport = useMemo(() => {
  //   return (
  //     <div className={styles.preset}>
  //       <div className={styles.presetSelect}>
  //         <Dropdown
  //           name="Preset"
  //           onChange={(value) => onChangePreset(value)}
  //           key="Preset"
  //           placeholder="Name preset"
  //           options={presets}
  //           hasError={false}
  //           isSimple
  //         />
  //       </div>
  //       <div className={styles.presetAction}>
  //         <BaseButton
  //           className={styles.presetBtn}
  //           text={
  //             <span>
  //               <EditIcon /> Edit
  //             </span>
  //           }
  //           skin="yellow"
  //           disabled={false}
  //           onClick={(value) => onClickEdit(value)}
  //         />
  //       </div>
  //     </div>
  //   )
  // }, [onClickEdit])

  if (isLoading && !countries.length) {
    return <FullLoader />
  }

  return (
    <Fragment>
      <Filter fields={filterFields} />
      <Accordeon title="Compressed metrics  table" initialState={true} content={<MetricList totals={totals} />} />
      <Accordeon
        title="Compressed metrics  graphic"
        skin="white"
        content={<LinearChart times={times} selectedMetric={selectedMetric} />}
        rightPart={rightPartMetrics}
      />
      <Accordeon
        title="Geographical distribution"
        skin="white"
        content={<MapChart countries={countries} from={filter.from} to={filter.to} />}
      />
      {/* <Accordeon
        title="Aggregated performance report"
        skin="white"
        content={<Report />}
        label={<Download url={'#'} />}
        rightPart={rightPartReport}
      /> */}
    </Fragment>
  )
}

export const StatisticAffiliate = StatisticAffiliateRaw
export default memo(StatisticAffiliate)
