import React, { memo, useCallback, useMemo } from 'react'
import cn from 'classnames'

import { getDictionary } from 'src/services'
import { commonConstants } from 'src/constants'

import { ReactComponent as CheckboxIcon } from 'src/assets/images/icons/checkbox.svg'

import styles from './styles.module.scss'
const { METRICS } = commonConstants

const MapSidebarRaw = ({ onChange, selectOptions }) => {
  const options = useMemo(() => {
    return getDictionary(METRICS)
  }, [])

  const onSelectValue = useCallback(
    (options) => {
      onChange(options)
    },
    [onChange],
  )

  const onClickOption = useCallback(
    (option, isSelected) => {
      if (isSelected) {
        const nextOption = selectOptions.filter((item) => {
          return item.id !== option.id
        })

        onSelectValue(nextOption)
      } else {
        onSelectValue([...selectOptions, option])
      }
    },
    [onSelectValue, selectOptions],
  )

  return (
    <div className={styles.root}>
      <div className={styles.title}>Map filters</div>
      <div className={styles.list}>
        {options.map((option) => {
          const { label, id } = option
          const isSelected = selectOptions.find((item) => {
            return item.id === id
          })

          return (
            <div
              key={id}
              className={cn(styles.item, {
                [styles.itemSelected]: isSelected,
              })}
            >
              <button
                className={cn(styles.option, {
                  [styles.optionSelected]: isSelected,
                })}
                onClick={() => onClickOption(option, isSelected)}
              >
                <div className={styles.status}>{isSelected && <CheckboxIcon stroke="#1E0E33" />}</div>
                <div className={styles.label}>{label}</div>
              </button>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export const MapSidebar = memo(MapSidebarRaw)
export default MapSidebar
