import React, { memo } from 'react'

import PageLayout from 'src/components/PageLayout'

import StatisticManager from '../StatisticManager'
import StatisticAffiliate from '../StatisticAffiliate'
import { useMappedState } from './bindings'

const StatisticRaw = () => {
  const { isManager, isAffiliate } = useMappedState()

  if (isManager) {
    return (
      <PageLayout>
        <StatisticManager />
      </PageLayout>
    )
  }

  if (isAffiliate) {
    return (
      <PageLayout>
        <StatisticAffiliate />
      </PageLayout>
    )
  }

  return null
}

export const Statistic = memo(StatisticRaw)
export default Statistic
