import React, { useState, useMemo, memo } from 'react'
import { ResponsiveContainer, ComposedChart, Line, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip } from 'recharts'

import { getDictionary, getDictionaryByCode } from 'src/services'
import { commonConstants } from 'src/constants'

import { ReactComponent as CheckboxIcon } from 'src/assets/images/icons/checkbox.svg'

import styles from './styles.module.scss'

const { METRICS } = commonConstants

const colorLegend = {
  APV: '#376FFF',
  CLK: '#00D6B4',
  EPC: '#df4e4e',
  EPL: '#5f32a8',
  LDS: '#FFC530',
  RVN: '#DF4ECA',
}

const getLegendData = () => {
  const legend = {}
  const metrics = getDictionary(METRICS)

  metrics.forEach((item) => {
    legend[item.id] = {
      color: colorLegend[item.id],
      isActive: true,
    }
  })
  return legend
}

const LinearChartRaw = ({ times, selectedMetric }) => {
  const mainMetric = 'LDS'
  const [legendData, setLegendData] = useState(getLegendData())

  const chartData = useMemo(() => {
    return times.map((item) => {
      const { key, metrics } = item
      const nextData = { key }

      Object.keys(metrics).forEach((key) => {
        if (legendData[key].isActive) {
          nextData[key] = metrics[key]
        }
      })

      return nextData
    })
  }, [times, legendData])

  const CustomizedDot = (props) => {
    const { cx, cy, stroke, payload, categoryColors, fillOpacity } = props
    const color = categoryColors[payload.color] || stroke

    return <circle cx={cx} cy={cy} r={4} stroke="#fff" strokeWidth={3} fill={color} fillOpacity={fillOpacity} />
  }
  console.log('legendData', legendData)
  const handleMouseEnter = (e) => {
    const dataKey = e.currentTarget.dataset.key
    if (dataKey) {
      if (legendData[dataKey].isActive) {
        setLegendData({ ...legendData, [dataKey]: { ...legendData[dataKey], isActive: false } })
      } else {
        setLegendData({ ...legendData, [dataKey]: { ...legendData[dataKey], isActive: true } })
      }
    }
  }
  const { color: mainColor, isActive: mainIsActive } = legendData[mainMetric] || {}

  return (
    <div className={styles.chart}>
      <div className={styles.chartBox}>
        <ResponsiveContainer width="100%" height="100%">
          <ComposedChart
            width={500}
            height={400}
            data={chartData}
            margin={{
              top: 20,
              right: 20,
              bottom: 20,
              left: 20,
            }}
          >
            <defs>
              <linearGradient id="colorLDS" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#FFC530" stopOpacity={0.1} />
                <stop offset="95%" stopColor="rgba(255, 197, 48, 0)" stopOpacity={0.1} />
              </linearGradient>
            </defs>
            <CartesianGrid stroke="#DDE0EB" />
            <XAxis dataKey="key" scale="band" stroke="#A2A7B8" />
            <YAxis
              label={{ value: getDictionaryByCode(METRICS, mainMetric), angle: -90, position: 'insideLeft' }}
              stroke="#A2A7B8"
              axisLine={false}
              tickLine={false}
            />
            <YAxis
              yAxisId="right"
              axisLine={false}
              tickLine={false}
              type="number"
              dataKey={selectedMetric.id}
              name={selectedMetric.id}
              orientation="right"
              stroke="#A2A7B8"
              label={{
                value: getDictionaryByCode(METRICS, selectedMetric.id),
                position: 'insideRight',
                offset: 0,
                angle: 90,
              }}
            />
            <Tooltip />
            <Bar dataKey="pv">
              {chartData.map((entry, index) => {
                const { color, isActive } = legendData[mainMetric] || {}
                return (
                  <Cell
                    key={`cell-${index}`}
                    stroke={color}
                    fill="url(#colorLDS)"
                    strokeWidth={2}
                    strokeOpacity={isActive ? 1 : 0}
                    fillOpacity={isActive ? 1 : 0}
                  />
                )
              })}
            </Bar>
            {Object.keys(legendData).map((key) => {
              console.log('key', key)
              if (key !== mainMetric) {
                const opacity = legendData[key].isActive ? 1 : 0

                return (
                  <Line
                    key={key}
                    type="monotone"
                    dataKey={key}
                    stroke={legendData[key].color}
                    dot={<CustomizedDot categoryColors={legendData[key].color} fillOpacity={opacity} />}
                    strokeOpacity={opacity}
                    fillOpacity={opacity}
                  />
                )
              }
              return null
            })}
          </ComposedChart>
        </ResponsiveContainer>
      </div>

      <div className={styles.legend}>
        <ul className={styles.list}>
          {Object.keys(legendData).map((key) => {
            const { color, isActive } = legendData[key] || {}
            console.log('key', key)
            if (key !== mainMetric) {
              return (
                <li className={styles.item} key={key}>
                  <button onClick={handleMouseEnter} data-key={key} className={styles.itemBtn}>
                    <span
                      className={styles.itemIcon}
                      style={{
                        background: color,
                      }}
                    >
                      {isActive && <CheckboxIcon stroke="#fff" />}
                    </span>

                    <span className={styles.itemLabel}>{getDictionaryByCode(METRICS, key)}</span>
                  </button>
                </li>
              )
            }
            return null
          })}

          <li className={styles.item}>
            <button onClick={handleMouseEnter} data-key={mainMetric} className={styles.itemBtn}>
              <span
                className={styles.itemIcon}
                style={{
                  background: mainColor,
                }}
              >
                {mainIsActive && <CheckboxIcon stroke="#fff" />}
              </span>
              <span className={styles.itemLabel}>{getDictionaryByCode(METRICS, mainMetric)}</span>
            </button>
          </li>
        </ul>
      </div>
    </div>
  )
}

export const LinearChart = memo(LinearChartRaw)
export default LinearChart
