import React, { memo } from 'react'
import cn from 'classnames'

import styles from './styles.module.scss'
import { ReactComponent as EnabledIcon } from 'src/assets/images/icons/enabled.svg'
import { ReactComponent as DisabledIcon } from 'src/assets/images/icons/disabled.svg'

const StatusRaw = (props) => {
  const { status } = props

  return <div className={cn(styles.status)}>{status === 'enabled' ? <EnabledIcon /> : <DisabledIcon />}</div>
}

export const Status = memo(StatusRaw)
export default Status
