const getColumns = () => {
  return [
    {
      name: 'ID',
      key: 'id',
      width: '18.09%',
    },
    {
      name: 'Name affiliate',
      key: 'name',
      width: '20.53%',
      isSearch: true,
    },
    {
      name: 'Clicks',
      key: 'CLK',
      width: '7.59%',
    },
    {
      name: 'Lead',
      key: 'LDS',
      width: '6.87%',
    },
    {
      name: 'Approved leads',
      key: 'APV',
      width: '15.84%',
    },
    {
      name: 'EPC',
      key: 'EPC',
      width: '6.43%',
    },
    {
      name: 'EPL',
      key: 'EPL',
      width: '6.25%',
    },
    {
      name: 'Revenue',
      key: 'RVN',
      width: '9.46%',
    },
    {
      name: 'Status',
      key: 'status',
      width: '8.94%',
    },
  ]
}
const prepareRow = (row) => {
  const { id, name, metrics, status } = row
  return {
    id,
    name,
    ...metrics,
    status,
  }
}

const getData = (items) => {
  return Object.keys(items).reduce((rows, rowId) => {
    const row = prepareRow(items[rowId])
    rows[rowId] = Object.keys(row).reduce((cells, cellKey) => {
      if (cellKey === 'name') {
        cells.push({
          value: row[cellKey],
          key: cellKey,
          type: 'LINK',
          to: `/statistics/${row.id}`,
        })
      } else if (cellKey === 'status') {
        cells.push({
          value: row[cellKey],
          key: cellKey,
          type: 'STATUS',
        })
      } else {
        cells.push({
          value: row[cellKey],
          key: cellKey,
          type: 'TEXT',
        })
      }

      return cells
    }, [])
    return rows
  }, {})
}

const makeTable = (items) => {
  return {
    columns: getColumns(items),
    data: getData(items),
  }
}

export default makeTable
