import React, { memo, useState, useCallback, useMemo } from 'react'
import isEmpty from 'lodash/isEmpty'

import Sidebar from './components/Sidebar'
import Map from './components/Map'

import styles from './styles.module.scss'

const MapSidebarRaw = ({ countries, from, to }) => {
  const [selectOptions, setSelectOptions] = useState([])

  const onChange = useCallback((metric) => {
    setSelectOptions(metric)
  }, [])

  const options = useMemo(() => {
    const selectIds = selectOptions.map((option) => {
      return option.id
    })

    const nextCountries = countries.map((country) => {
      const { metrics, ...other } = country

      if (isEmpty(metrics)) {
        return { ...other }
      }

      let nextMetrics = {}

      selectIds.forEach((id) => {
        if (metrics[id]) {
          nextMetrics[id] = metrics[id]
        }
      })

      if (!isEmpty(nextMetrics)) {
        return {
          ...other,
          metrics: nextMetrics,
        }
      }

      return { ...other }
    })

    const filteredCountries = nextCountries.filter((item) => item)

    return filteredCountries
  }, [selectOptions, countries])

  return (
    <div className={styles.root}>
      <div className={styles.left}>
        <Map countries={options} from={from} to={to} />
      </div>
      <div className={styles.right}>
        <Sidebar onChange={onChange} selectOptions={selectOptions} />
      </div>
    </div>
  )
}

export const MapSidebar = memo(MapSidebarRaw)
export default MapSidebar
