import React, { memo, useMemo } from 'react'
import ReactTooltip from 'react-tooltip'

import { getDictionaryByCode } from 'src/services'
import { commonConstants } from 'src/constants'

import styles from './styles.module.scss'

const { METRICS, COUNTRIES } = commonConstants

const TooltipRaw = ({ data, from, to }) => {
  const renderList = useMemo(() => {
    if (!data || !data.metrics) {
      return null
    }

    const metricsData = Object.keys(data.metrics).map((key) => {
      return {
        key,
        name: getDictionaryByCode(METRICS, key),
        value: data.metrics[key],
      }
    })

    return metricsData.map((metric) => {
      return (
        <div className={styles.item} key={metric.key}>
          {metric.name}:<span className={styles.value}>{metric.value}</span>
        </div>
      )
    })
  }, [data])

  return (
    <ReactTooltip
      className={styles.root}
      borderColor="#DDE0EB"
      textColor="#1E0E33"
      backgroundColor="#ffffff"
      arrowColor="#ffffff"
      delayHide={100000}
      border
    >
      {data && data.metrics ? (
        <div className={styles.box}>
          <div className={styles.country}>
            {getDictionaryByCode(COUNTRIES, data.country)} ({data.country})
          </div>
          <div className={styles.date}>
            {from}  {to}
          </div>
          <div className={styles.list}>{renderList}</div>
        </div>
      ) : null}
    </ReactTooltip>
  )
}

export const Tooltip = memo(TooltipRaw)
export default Tooltip
