import { useMapActions, useMapState } from 'src/hooks'
import { modalThunks, authThunks, statsThunks } from 'src/thunks'
import { statisticsSelectors } from 'src/selectors'

export const useMappedState = () => {
  return useMapState((state) => {
    return {
      ...statisticsSelectors.getStatistics(state),
    }
  })
}

export const useMappedActions = () => {
  return useMapActions({
    showModal: modalThunks.showModal,
    authUser: authThunks.authUser,
    login: authThunks.login,
    loadStats: statsThunks.loadStats,
  })
}
