import React, { memo, Fragment, useState, useMemo, useEffect, useCallback } from 'react'
import moment from 'moment'

import { getDictionary } from 'src/services'
import { commonConstants } from 'src/constants'

import Filter from 'src/indyComponents/Filter'
import FullLoader from 'src/components/FullLoader'
import Table from 'src/indyComponents/Table'

import * as services from './services'
import { useMappedActions, useMappedState } from './bindings'
import styles from './styles.module.scss'
const { BRANDS } = commonConstants

const StatisticManagerRaw = () => {
  const { isLoading, items } = useMappedState()
  const actions = useMappedActions()

  const [filter, setFilter] = useState({
    from: moment().subtract(29, 'days').format('YYYY-MM-DD'),
    to: moment().format('YYYY-MM-DD'),
    affiliate: [],
    brand: [],
  })

  useEffect(() => {
    console.log('filter', filter)
    actions.loadAffiliates(filter)
  }, [actions, filter])

  const onChangeDate = useCallback(
    (value) => {
      const { from, to } = value
      const fromNext = from.format('YYYY-MM-DD')
      const toNext = to.format('YYYY-MM-DD')
      setFilter({ ...filter, from: fromNext, to: toNext })
    },
    [filter, setFilter],
  )

  const onChangeAffiliate = useCallback(
    (value) => {
      const nextAffiliate = value.map((item) => item.id)
      setFilter({ ...filter, affiliate: nextAffiliate })
    },
    [filter, setFilter],
  )

  const onChangeBrands = useCallback(
    (value) => {
      const nextBrands = value.map((item) => item.id)
      setFilter({ ...filter, brand: nextBrands })
    },
    [filter, setFilter],
  )

  const allAffiliate = useMemo(() => {
    return [
      {
        id: 'name1',
        label: 'name1',
        value: 'name1',
      },
    ]
  }, [])

  const filterFields = useMemo(() => {
    return [
      {
        type: 'date_range',
        label: 'Data Range',
        onChange: onChangeDate,
      },
      {
        type: 'affiliate',
        label: 'Name affiliate',
        placeholder: 'All names',
        options: allAffiliate,
        onChange: onChangeAffiliate,
      },
      {
        type: 'brands',
        label: 'Brands',
        options: getDictionary(BRANDS),
        hasSearch: true,
        placeholderSearch: 'Search brand...',
        placeholder: 'All brands',
        onChange: onChangeBrands,
      },
    ]
  }, [onChangeDate, onChangeAffiliate, onChangeBrands, allAffiliate])

  const { columns, data } = useMemo(() => {
    return services.makeTable(items)
  }, [items])

  return (
    <Fragment>
      <Filter fields={filterFields} />
      <div className={styles.main}>
        {isLoading ? (
          <FullLoader />
        ) : (
          <div className={styles.section}>
            <div className={styles.sectionHead}>
              <div className={styles.sectionTitle}>Users table</div>
            </div>

            <div className={styles.sectionBody}>
              <Table columns={columns} data={data} />
            </div>
          </div>
        )}
      </div>
    </Fragment>
  )
}

export const StatisticManager = memo(StatisticManagerRaw)
export default StatisticManager
